.App {
  text-align: center;
}

nav {
  display: flex; 
  justify-content: space-between; 
  padding: 10px 0;
}

button {
  padding: 10px 20px;
  color: #232424;
  border: none;
  background-color: #C5DEDE;
  border-radius: 25px;
}

button:hover {
  color: #C5DEDE;
  background-color: #232424;
  cursor: pointer;
  transition: ease-in;
}
